    src: url('//design.yonyoucloud.com/static/svgicon/iconfont.eot') format('embedded-opentype'), /* IE6-IE8 */
    url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2'),
    url('//design.yonyoucloud.com/static/svgicon/iconfont.woff') format('woff'),
    url('//design.yonyoucloud.com/static/svgicon/iconfont.ttf') format('truetype'), /* chrome, firefox, opera, Safari, Android, iOS 4.2+ */
    url('//design.yonyoucloud.com/static/svgicon/iconfont.svg') format('svg'); /* iOS 4.1- */
}

